body {
  margin: 0;
  padding: 0;
  font-family: "Titillium Web";
}

.container {
  padding-left: 200px;
  padding-right: 200px;
}

.row [class^="col-"] {
  padding: 20px 10px 10px 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}

@media (min-width: 760px) {
  .row {
    display: flex;
  }
  .row .col-1-8 {
    width: 25%;
  }
  .row .col-2-8 {
    width: 50%;
  }
  .row .col-3-8 {
    width: 75%;
  }
  .row .col-4-8 {
    width: 100%;
  }
}

.page-header {
  background-color: #87509c;
  padding-top: 50px;
}

.page-header .header__flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-header .navigation__list {
  list-style: none;
  display: flex;
}

.page-header .navigation__list .navigation__item {
  box-sizing: border-box;
}

.page-header .navigation__list .navigation__item a {
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  padding: 10px 20px;
  transition: background-color 0.1s, border-radius 0.1s;
}

.page-header .navigation__list .navigation__item a:hover {
  background-color: #643a79;
  border-radius: 5px;
}

.welcome {
  background-color: #87509c;
  height: calc(100vh - 122px);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #f7f3ea;
  text-align: center;
}

.welcome .welcome__header {
  font-size: 40px;
}

.welcome .welcome__button {
  margin-top: 60px;
  border: none;
  border-radius: 5px;
  background-color: #eb7d4b;
  color: #fff;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: bolder;
  padding: 25px 70px;
  box-sizing: border-box;
  transition: background-color .1s .1s ease-in-out;
  cursor: pointer;
}

.welcome .welcome__button:focus {
  outline: none;
  border: 2px dashed #fff;
}

.welcome .welcome__button:hover {
  background-color: #bb4915;
}
