$gap: 10px;
$breakpoint: 760px;
$column: 4;

.row {
  [class^="col-"] {
    padding: 20px 10px 10px 10px;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  @media (min-width: $breakpoint) {
    display: flex;

    @for $i from 1 through $column {
      .col-#{$i}-8 {
        width: 100% / $column * $i;
      }
    }
  }
}
